.character {
    margin-bottom: 2px;
    margin-right: 2px;
    position: relative;
}

.id {
    font-size: 10px;
}

.ar {}

.right {
    float: right;
    width: 300px;
    border: 3px solid #73AD21;
    padding: 10px;
}

.character-wrapper {
    display: flex;
}

span.character {
    position: relative;
}

.character>img {
    width: 40px;
    height: 40px;
}

.constellation {
    position: absolute;
    text-align: center;
    top: 0;
    right: 0;
    width: 18px;
    height: 18px;
    background: rgba(0, 0, 0, .5);
    border-radius: 0 4px 0 4px;
    color: #fff;
    line-height: 20px;
    padding-left: 3px;
    font-size: 10px;
    background: transparent url(../public/static/images/constellation_bg.png) no-repeat 50%/contain;
    font-family: "Tahoma Regular", "Tahoma Bold", sans-serif !important;
}

.grey {
    filter: grayscale(70%);
    background-color: rgba(128, 128, 128, 1);
}

.light {
    background-color: rgb(213 170 16);
}

.filter-icon {
    width: 28px;
}